						},
						{
							value: 'taiwan',
							label: '',
						},
					],
				},
			],
			pickerOptions0: {
				shortcuts: [
					{
						text: '',
						onClick(picker) {
							const end = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							const start =
								new Date(
									new Date(
										new Date().toLocaleDateString()
									).getTime()
								) -
								3600 * 1000 * 24 * 1;
							picker.$emit('pick', [start, end]);
						},
					},
					{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							picker.$emit('pick', [start, end]);
						},
					},
					{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							start.setTime(
								start.getTime() - 3600 * 1000 * 24 * 6
							);
							picker.$emit('pick', [start, end]);
						},
					},
					{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							start.setTime(
								start.getTime() - 3600 * 1000 * 24 * 29
							);
							picker.$emit('pick', [start, end]);
						},
					},
				],
				disabledDate(time) {
					return time.getTime() > Date.now();
				},
			},
			errarrs: '',
			errarrs2: '',
		};
	},
	components: {
		fenye,
	},
	filters: {
		settimes(data) {
			if (data) {
				var stat = getymdtime(data);
				return stat;
			} else {
				return data;
			}
		},
	},
	created() {},
	mounted() {
		if (this.$cookies.get('id')) {
			this.chanid = this.$cookies.get('id') * 1;
		} else {
			this.$router.push({ path: '/' });
		}
		// this.gettoken();
	},
	methods: {
		//token
		gettoken() {
			this.tableData = [];
			let parmas = new Object();
			parmas.chanid = this.chanid;
			parmas.pagesize = 10;
			parmas.page = 0;
			parmas.buser_id = this.chanid + '';
			getterminal(parmas)
				.then((res) => {
					if (res.status == 0) {
						if (res.result.cols.length <= 0) {
							this.$alert(
								'',
								'',
								{
									confirmButtonText: '',
									showClose: false,
									center: true,
									callback: (action) => {
										this.$router.push({
											path: '/terminal_management',
										});
									},
								}
							);
						}
					}
				})
				.catch((err) => {});
		},
		handleChange() {
			var thsAreaCode = this.$refs.cascaderAddr.getCheckedNodes()[0]
				.pathLabels;
		},
		removeEmpty(arr) {
			for (var i = 0; i < arr.length; i++) {
				if (arr[i] == '' || typeof arr[i] == 'undefined') {
					arr.splice(i, 1);
					i = i - 1; // i - 1 , 2 
				}
			}
			return arr;
		},
		getBLen(str) {
			if (str == null) return 0;
			if (typeof str != 'string') {
				str += '';
			}
			return str.replace(/[^\x00-\xff]/g, '01').length;
		},
		//---
		getrefresh(datas) {
			let parmas = new Object();
			parmas.buser_id = this.chanid + '';
			if (datas == 0) {
				parmas.type = 0;

				if (this.textarea1 == '') {
					this.$message.error('');
					return false;
				} else {
					if (
						this.removeEmpty(this.textarea1.split('\n')).length > 10
					) {
						this.$message.error('10');
						return false;
					} else {
						let urlyure = this.removeEmpty(
							this.textarea1.split('\n')
						);

						let err_num = 0;
						// var resyzm = /^http(s)?:\/\/[^\u4e00-\u9fa5]{1,1020}$/;
						var resyzm = /^[\u4e00-\u9fa5a-zA-Z0-9]{1,1024}$/;
						this.errarrs = '';
						urlyure.forEach((item, index) => {
							if (resyzm.test(item) === false) {
								this.errarrs = this.errarrs.concat(
									item + '</br>'
								);
								err_num++;
							} else if (this.getBLen(item) > 1024) {
								this.errarrs = this.errarrs.concat(
									item + '</br>'
								);
								err_num++;
							}
						});
						if (err_num != 0) {
							this.$alert(
								`${this.errarrs}`,
								'',
								{
									dangerouslyUseHTMLString: true,
								}
							);
							return false;
						} else {
							parmas.url_name = urlyure;
						}
					}
				}
				if (this.citylabel == '' || this.citylabel.length < 2) {
					this.$message.error('');
					return false;
				} else {
					parmas.area = this.citylabel[1];
				}
			} else {
				parmas.type = 1;
				if (this.textarea2 == '') {
					this.$message.error('');
					return false;
				} else {
					let urllist = this.removeEmpty(this.textarea2.split('\n'));
					if (urllist.length > 10) {
						this.$message.error('10');
						return false;
					} else {
						let err_num = 0;
						// var resyzm = /^http(s)?:\/\/[^\u4e00-\u9fa5]{1,1020}$/;
						var resyzm = /^[\u4e00-\u9fa5a-zA-Z0-9]{1,1024}$/;
						this.errarrs2 = '';
						urllist.forEach((item, index) => {
							if (resyzm.test(item) === false) {
								this.errarrs2 = this.errarrs2.concat(
									item + '</br>'
								);
								err_num++;
							} else if (this.getBLen(item) > 1024) {
								this.errarrs2 = this.errarrs2.concat(
									item + '</br>'
								);
								err_num++;
							}
						});
						if (err_num != 0) {
							this.$alert(
								`${this.errarrs2}`,
								'',
								{
									dangerouslyUseHTMLString: true,
								}
							);
							return false;
						} else {
							parmas.url_name = urllist;
						}
					}
				}
				if (this.citylabel1 == '' || this.citylabel1.length < 2) {
					this.$message.error('');
					return false;
				} else {
					parmas.area = this.citylabel1[1];
				}
			}
			parmas.buser_id = this.chanid + '';
			resource_refresh(parmas)
				.then((res) => {
					if (res.status == 0) {
						if (res.data.failed_count == 0) {
							this.$message({
								type: 'success',
								message: '!',
							});
							if (res.err_code == 165156446464) {
								//
								if (parmas.type == 0) {
									this.$confirm(
										'',
										'',
										{
											confirmButtonText: '',
											cancelButtonText: '',
											type: 'warning',
										}
									)
										.then(() => {
											this.activeName = 'second';
										})
										.catch(() => {
											// this.$message({
											//   type: "info",
											//   message: ""
											// });
										});
								} else {
									this.$confirm(
										'',
										'',
										{
											confirmButtonText: '',
											cancelButtonText: '',
											type: 'warning',
										}
									)
										.then(() => {
											this.$message({
												type: 'success',
												message:
													'!',
											});
										})
										.catch(() => {
											// this.$message({
											//   type: "info",
											//   message: ""
											// });
										});
								}
							}
						} else {
							this.errarr = '';
							res.data.res_data.forEach((item, index) => {
								this.errarr = this.errarr.concat(
									item[0] + '</br>'
								);
							});
							this.$alert(
								`${this.errarr}`,
								res.data.failed_count + '',
								{
									dangerouslyUseHTMLString: true,
								}
							);
						}
					} else {
						this.error.$message('' + res.msg);
					}
				})
				.catch((err) => {});
		},
		seachdata() {
			this.getrefreshstate();
		},
		//--
		getrefreshstate() {
			this.tableData = [];
			let parmas = new Object();
			parmas.url_name = this.input;
			parmas.buser_id = this.chanid + '';
			parmas.refresh_type = this.valuea;
            parmas.state = parseInt(this.valueb);
            parmas.page=this.currentPage-1;
            parmas.order=this.order;
			if (this.value1 == '') {
				parmas.start_time = 0;
				parmas.end_time = 0;
			} else {
				parmas.start_time = dateToMs(this.value1[0]);
				parmas.end_time = dateToMs(this.value1[1]);
			}
			refresh_state(parmas)
				.then((res) => {
					if (res.status == 0) {
                        this.total_cnt=res.data.total;
						if (res.data.result.length > 0) {
                            this.tableData = res.data.result;
                            
						} else {
							this.$message({
								message: '',
								type: 'warning',
							});
						}
					} else {
						this.$message.error(res.err_msg);
					}
				})
				.catch((err) => {});
		},
        //
		tableSortChange(column) {
            this.currentPage = 1;
            console.log(column.order);
			if (column.order == 'descending') {
				this.order = 1;
			} else {
				this.order = 0;
			}
			this.getrefreshstate();
		},
		handleClick(tab, event) {
			this.textarea1 = '';
			this.textarea2 = '';
			if (tab.name == 'third') {
				this.getrefreshstate();
			}
		},
		getpage() {},
		gettol() {},
		//
		option_display() {
			this.optiondisplay = !this.optiondisplay;
			this.rotate = !this.rotate;
		},
		//
