			// this.optiondisplay = false;
			this.getrefreshstate();
		},
		//
		reset() {
			this.input = '';
			this.valuea = '';
			this.valueb = -1;
			this.value1 = '';
			this.getrefreshstate();
		},
		// 
		headClass() {
			return 'text-align: center;background:#F3F6FB;color:#333333;font-size:16px;';
		},
		// 
		rowClass() {
			return 'text-align: center;';
		},
	},
};
</script>

<style lang="scss" scoped>
.content {
	.yure_content {
		width: 100%;
		// margin: 15px 0;
	}
	width: 100%;
	height: 100%;
	text-align: left;
	.seach {
		width: 100%;
		// display: flex;
		// align-items: center;
		.seach_top {
			width: 100%;
			height: 60px;
			line-height: 60px;
			.input-with-select {
				width: 300px;
				float: left;
				margin-right: 10px;
			}
		}
		.seach_bottom {
			text-align: left;
			height: 80px;
			background: #f2f6fa;
			border-radius: 10px;
			padding: 10px 15px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			margin-bottom: 20px;
			span {
				color: #999999;
				font-size: 14px;
				margin-left: 10px;
			}
		}
	}
}
//
.aa {
	transition: all 1s;
}
.go {
	transform: rotate(-180deg);
	transition: all 1s;
}
</style>



