    // ,
    changePageCoreRecordData(a, b, c, d) {
        //debugger
        // 
        let idKey = d;
        // let that = this;
        // 
        // console.log(a)
        if (a.length <= 0) {
            a = b;
            return a;
        }
        // key
        let selectAllIds = [];
        a.forEach(row => {
            selectAllIds.push(row[idKey]);
        });
        let selectIds = [];
        // id
        b.forEach(row => {
            selectIds.push(row[idKey]);
            // 
            if (selectAllIds.indexOf(row[idKey]) < 0) {
                a.push(row);
            }
        });
        let noSelectIds = [];
        // id
        c.forEach(row => {
            if (selectIds.indexOf(row[idKey]) < 0) {
                noSelectIds.push(row[idKey]);
            }
        });
        noSelectIds.forEach(id => {
            if (selectAllIds.indexOf(id) >= 0) {
                for (let i = 0; i < a.length; i++) {
                    if (a[i][idKey] == id) {
                        // 
                        a.splice(i, 1);
                        break;
                    }
                }
            }
        });

        return a;
    },
    //COOKIE
    get: function(name) {
        var v = window.document.cookie.match(
            '(^|;) ?' + name + '=([^;]*)(;|$)'
        );

        return v ? v[2] : null;
    },
    //component
    trans: function trans(tree, keymap) {
        for (var i = 0; i < tree.length; i++) {
            tree[i].component = keymap[tree[i].component];
            // console.log(tree[i].component)
            //console.log(keymap[tree[i].component])
            if (tree[i].children) {
                tree[i].children = this.trans(tree[i].children, keymap);
            }
        }
        return tree;
    }
